@use "global" as *;

.blog-details__main {
    margin-top: rem(50);
}

.blog-details {
    margin-top: rem(53);
}

.blog-details__inner {

}

.blog-details__top {
    margin-left: rem(20);
    margin-right: rem(20);
}

.blog-details__title {
        font-size: rem(20);
        letter-spacing: 0.02em;
        line-height: 1.75;
}

.blog-details__info {
    display: flex;
    margin-top: rem(16);
}

.blog-details__date {
    letter-spacing: 0.025em;
    line-height: 2;
    margin-right: rem(30);
    padding-top: rem(5);
}

.blog-details__category {
    letter-spacing: 0.125em;
    border: 1px solid $white;
    padding: rem(8) rem(20);
}

.blog-details__img-1 {
    padding-top: 66.667%;
    position: relative;
    margin-top: rem(20);
}

.blog-details__img-1 img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.blog-details__lists {
    
    ul {
        list-style-type: disc;
    }

    ol {
        
    }
}